define('view/pieces/segs/elements/demo-tip',[],function(){
	function Demo(){

		$("button.btn-primary.btn-info").click(function(){
			$(".highlight").addClass('hidden')
			$("#btn-info").removeClass('hidden')
			showTip({
				content:"10030",
				closeCallback:function(){//tip
					console.log(" info");
				}
			});
		});

		$("button.btn-primary.btn-warning").click(function(){
			$(".highlight").addClass('hidden')
			$("#btn-warning").removeClass('hidden')
			showTip({
				type:'warning',
				content:"10030",
				closeCallback:function(){//tip
					console.log(" waning");
				}
			});
		});

		$("button.btn-primary.btn-success").click(function(){
			$(".highlight").addClass('hidden')
			$("#btn-success").removeClass('hidden')
			showTip({
				type:'success',
				content:"",
				closeCallback:function(){//tip
					console.log(" waning");
				}
			});
		});

		$("button.btn-primary.btn-danger").click(function(){
			$(".highlight").addClass('hidden')
			$("#btn-danger").removeClass('hidden')
			showTip({
				type:'danger',
				content:"",
				closeCallback:function(){//tip
					console.log(" waning");
				}
			});
		});


		$("button.btn-success2").click(function(){
			showTip({
				type:"success",
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true,
				closeCallback:function(){//tip
					console.log(" success");
				},
				clickCallback:function(ta,tip){//x tip
					if(ta.tagName=="BUTTON" && $(ta).hasClass("btn2")){
						console.log("tip");
					}
				}
			});
		});

		$("button.btn-danger2").click(function(){
			showTip({
				time:null,//  30 30s
				type:"danger",
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true,
				closeCallback:function(){//tip
					console.log(" success");
				},
				clickCallback:function(ta,tip){//x tip
					if(ta.tagName=="BUTTON" && $(ta).hasClass("btn2")){
						console.log("tip");
					}
				}
			});
		});

		$("button.btn-info2").click(function(){
			showTip({
				through:false,
				type:"info",
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true
			});
		})

		$("button#temp-btn").click(function(){
			showTip({
				time:null,//  30 30s
				type:"warning",
				bind:$(".temp-window"),//DOM jquery
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true
			});
		});


	}
	return Demo;
});
