define('view/pieces/segs/elements/demo-search',[],function(){
	function Demo(){
			$(".ndp-search-wrapper[name=plain]").search({
				placeholder:"",
				name:"username"//serialize name 
			}).val("hello");//input ;
			
			$(".ndp-search-wrapper[name=disable]").search({
				placeholder:"",
				disabled:true
			});
			
			
			$(".ndp-search-wrapper[name=mix]").search({
				type:2,
				dropList:[{text:""},{text:""},{text:""}]
			}).on("SCOPE_CHANGE",function(e){//
				// 
				console.log(e.originalEvent.data);//{index:value:}
			});	
		
			$(".ndp-search-wrapper[name=plain2]").search({
				type:3,
				ajaxOptions: {
					type: "GET",
					url: "../data/search.json"
				}				
			}).on("INPUT_CHANGE",function(e){// 
				console.log(e.originalEvent.data);//{text:}
			}).on("do_search",function(e){
				// () 
				console.log(e.originalEvent.data);//
			}).on("ITEM_SELECT",function(e){//ajax 
				console.log(e.originalEvent.data);//{text:}
			}).on("INPUT_CLEAR",function(e){//x 
				console.log(e.originalEvent.data);//{text:}
			});
			
			$(".ndp-search-wrapper[name=mixall]").search({
				type:4,
				dropList:[{text:"china"},{text:"USA"},{text:""}],
				ajaxOptions: {
					type: "GET",
					url: "../data/search.json",
					xhrFields: { withCredentials: true}
				}				
			}).on("input_change",function(e){
				console.log(e.originalEvent.data);
			}).on("do_search",function(e){
				// () 
				console.log(e.originalEvent.data);//
			}).on("INPUT_CHANGE",function(e){// 
				console.log(e.originalEvent.data);//{text:}
			}).on("ITEM_SELECT",function(e){//ajax 
				console.log(e.originalEvent.data);//{text:}
			}).on("INPUT_CLEAR",function(e){//x 
				console.log(e.originalEvent.data);//{text:}
			});		
	}
	return Demo;
});
