define('view/pieces/segs/elements/demo-tabs',[],function(){
	function Demo(){
		$("div.ndp-tab-wrapper[name=plain-tab]").tabs({
			list:["hello","world","china"]
		}).on("TAB_CHANGE",function(e){//tab
			console.log(e.originalEvent.data);//{index: tab(0) int,name:tab string}
		}).val(1);//tab 0

		$("div.ndp-tab-wrapper[name=badge-tab]").tabs({
			list:[{text:"China",badge:20},{text:"USA",badge:999},{text:"CANADA",badge:10}],
			badge:true//
		});

		$("div.ndp-tab-wrapper[name=rm-tab]").tabs({
			list:[{text:"China",badge:20},{text:"USA",badge:999},{text:"CANADA",badge:10}],
			badge:false,
			rm:true//tabX
		}).on("TAB_CHANGE",function(e){//tab
			console.log(e.originalEvent.data);//{index: tab(0) int,name:tab string}
		}).on("TAB_REMOVED",function(e){//x 
			console.log(e.originalEvent.data);//{rm: , active: , current: }
		});
		
		$("div.ndp-tab-wrapper[name=more-tab]").tabs({
			list:[{text:"",badge:20},
				  {text:"",badge:999},
				  {text:"",badge:10},
				 {text:"",badge:10},
				 {text:"",badge:10},
				 {text:"",badge:10}],
			type:2
		});			
	}
	return Demo;
});
