define('view/pieces/segs/elements/demo-page',[],function(){
	function Demo(){
		$("div.ndp-page-wrapper[name=plain-page]").page({
			totalPages:15
		}).on("PAGE_CHANGE",function(e){// 
			console.log(e.originalEvent.data);//{currentPage:  int}
		});
		
		$("div.ndp-page-wrapper[name=drop-page]").page({
			type:2,//1 2 
			totalItems:200,//n
			perPage:10//10
		}).on("SHOW_ITEMS_CHANGE",function(e){
			console.log(e.originalEvent.data);//{currentPage:int,  perpage:int}
		}).on("PAGE_CHANGE",function(e){
			console.log(e.originalEvent.data);
		});
		
		$("div.ndp-page-wrapper[name=drop-page2]").page({
			type:3,//1 2 3  + 
			totalItems:200,//n
			perPage:10//10
		}).on("PAGE_CHANGE",function(e){
				console.log(e.originalEvent.data);
		});	
		
		$("div.ndp-page-wrapper[name=drop-page3]").page({
			type:4,//4 
			totalPages:100,//n
		});			
	}
	return Demo;
});
